import React, {useState} from 'react';
import {Link, useNavigate} from 'react-router-dom';
import * as boards from "../../apis/boards";

const BoardInsert = () => {
    // state 
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');

    // useNavigate 
    const navigate = useNavigate();
    const onInsert = async (title, content) => {
        try {
            const response = await boards.insert(title, content);
            alert(" ");

            // ->    ( X)
            navigate('/boards');
        } catch (e) {
            console.log(e);
        }
    }

    return (
        <div>
            <h1> </h1>
            <table>
                <tbody>
                    <tr>
                        <td></td>
                        <td>
                            <input type='text' value={title} onChange={e => setTitle(e.target.value)} />
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            <textarea cols='40' rows='5' value={content} onChange={e => setContent(e.target.value)} />
                        </td>
                    </tr>
                </tbody>
            </table>
            <div>
                <button onClick={() => onInsert(title, content)}></button>
                <Link to="/boards"></Link>
            </div>
        </div>
    );
};

export default BoardInsert;