import React, {useState, useEffect} from "react";
import '../../App.css';
import '../../style/Auth.css'
import * as auth from "../../apis/auth";
import Logo from "../../utils/Logo";
import InputBox from "./utils/InputBox";
import {emailRegex} from "./utils/Regex";
import SocialLoginBtns from "./utils/SocialLoginBtns";
import {Link, useNavigate} from 'react-router-dom';

function Login({message = null, isDarkMode, loginName, setLoginName}) {
    const [username, setUsername] = useState("");
    const [usernameMessage, setUsernameMessage] = useState("");
    const [password, setPassword] = useState("");
    const [passwordMessage, setPasswordMessage] = useState("");

    const navigate = useNavigate();

    useEffect(() => {
        //   home 
        const isLogin = window.localStorage.getItem("username");
        if (isLogin) {
            navigate('/');
        }
    }, []);

    //  
    function validation() {
        if (!username || !password) {
            if (!username) {
                setUsernameMessage(" .");
            }
            if (!password) {
                setPasswordMessage(" .");
            }
            return;
        }

        if (!emailRegex(username)) {
            setUsernameMessage("  .");
            return;
        }
    }

    //  
    function messageReset() {
        setUsernameMessage("");
        setPasswordMessage("");
    }

    //  
    const login = async () => {
        messageReset();
        validation();
        try {
            const response = await auth.login(username, password);
            if (response.token) {
                //    username 
                localStorage.setItem("ACCESS_TOKEN", response.token);
                localStorage.setItem("username", username);
                setLoginName(username);
                // token    
                navigate('/');
            }
        } catch (error) {
            setPasswordMessage("    .");
            setUsernameMessage("    .");
        }
    }

    return (
        <div className="auth-container">
            {/*   (ex.   ,   )*/}
            <div className="message">{message}</div>
            <Logo isDarkMode={isDarkMode}/>
            <div className="login-wrap">
                <InputBox
                    className={"login"}
                    type={"email"}
                    id={"email-input-box"}
                    placeholder={""}
                    value={username}
                    setValue={setUsername}
                    onKeyPress={login}
                    message={usernameMessage}
                />
                <InputBox
                    className={"password"}
                    type={"password"}
                    id={"password-input-box"}
                    placeholder={""}
                    value={password}
                    setValue={setPassword}
                    onKeyPress={login}
                    message={passwordMessage}
                />
                <button className="login-btn" onClick={login}></button>
                <div className="link-wrap">
                    <Link className="signup" to="/signup"></Link>
                    {/*<a className="find-password" onClick={() => alert(" .  .")}>*/}
                    {/*    </a>*/}
                </div>
            </div>
            <div className="bar"></div>
            <SocialLoginBtns/>
        </div>
    );
}

export default Login;