import React, {useState} from "react";
import * as auth from "../../apis/auth";
import InputBox from "./utils/InputBox";
import {Link, useNavigate} from "react-router-dom";

function SignUpCharacters({setIsLoading}) {
    const [apiKey, setApiKey] = useState("");
    const [apiKeyMessage, setApiKeyMessage] = useState("");
    const [character, setCharacter] = useState("");
    const [characterMessage, setCharacterMessage] = useState("");

    const navigate = useNavigate();

    //  
    function messageReset() {
        setApiKeyMessage("");
        setCharacterMessage("");
    }

    //  
    function validation() {
        if (!apiKey || !character) {
            if (!apiKey) {
                setApiKeyMessage("ApiKey .");
            }
            if (!character) {
                setCharacterMessage(" .");
            }
            return;
        }
    }

    const addCharacter = async () => {
        messageReset();
        validation();
        try {
            setIsLoading(true);
            await auth.addCharacters(apiKey, character);
            alert(".");
            navigate("/");
        } catch (error) {
            if (error.errorMessage === "   .") {
                setCharacterMessage(error.errorMessage);
            } else if (error.errorMessage === "  apiKey ." ||
                error.errorMessage === " (1 100) .") {
                setApiKeyMessage(error.errorMessage);
            } else {
                alert(error.errorMessage);
            }
        } finally {
            setIsLoading(false);
        }
    };


    return (
        <div className="auth-container">
            <div className="mention">
                <p>  </p>
            </div>
            <div className="signup-wrap">
                <InputBox
                    className={"apikey"}
                    type={"text"}
                    id={"apikey-input-box"}
                    placeholder={" ApiKey"}
                    value={apiKey}
                    setValue={setApiKey}
                    onKeyPress={addCharacter}
                    message={apiKeyMessage}
                />
                <InputBox
                    className={"character"}
                    type={"text"}
                    id={"character-input-box"}
                    placeholder={" "}
                    value={character}
                    setValue={setCharacter}
                    onKeyPress={addCharacter}
                    message={characterMessage}
                />
                <button className="login-btn" onClick={addCharacter}>  </button>
                <div className="link-wrap">
                    <Link className="signup" to="/"></Link>
                </div>
            </div>
        </div>
    );
};

export default SignUpCharacters;
